import React, { useState } from 'react'


function Planel({ title, children }) { 
  const [isAcitive, setIsActive] = useState(false);
  return (
    <div style={{
      width: 400,
      border: '1px solid black',
      padding:10
    }}>
      <h3>{title}</h3>
      {isAcitive ? 
        <p>{children}</p>
        : <button onClick={() => {setIsActive(true)}}></button>
      }
    </div>
  )
}

export default function StateIndependence() {
  return (
    <>
      <h2></h2>
      <Planel title=''>
      200 1929  1997 
      </Planel>
      <Planel title=''>
       <span lang="kk-KZ"></span><i lang="la">Malus sieversii</i> 
      </Planel>
    </>
  )
}
