import { useState } from "react";

export default function AddTask({ onAddTask }) { 
  const [addText, setAddText] = useState('');
  return (
    <div>
      <input
        type="text"
        placeholder=""
        value={addText}
        onChange={e => setAddText(e.target.value)}
      />
      {" "}
      <button onClick={() => {onAddTask(addText)}}></button>
    </div>
  )
}