import React from 'react'

function Item({name,isPacked}){
  return (
    <li>
      {isPacked ? 
        <del>
          {name + ' '}
        </del> :
        name
      }
    </li>
  )
}
export default function JudgeDel() {
  return (
    <div>
      <h1>Sally Ride</h1>
      <ul>
        <Item name='' isPacked={true}/>
        <Item name='' isPacked={true}/>
        <Item name='Tam' isPacked={false}/>
      </ul>
    </div>
  )
}
