import React from 'react'

function Item({name,isPacked}){
  if (isPacked) { 
    return <li>{name} </li>
  }
  return  <li>{name}</li>
}

export default function Judge() {
  return (
    <div>
      <h1>Sally Ride</h1>
      <ul>
        <Item name='' isPacked={true}/>
        <Item name='' isPacked={true}/>
        <Item name='Tam' isPacked={false}/>
      </ul>
    </div>
  )
}
