import { useRef } from "react";

function Button({ onClick, children }) {
  const ref = useRef(null);
  return (
    <button onClick={() => {
      clearTimeout(ref.current);
      ref.current = setTimeout(() => {
        onClick();
      }, 1000);
    }}>
      {children}
    </button>
  );
}
