import React from 'react'

function Item({name,isPacked}){
  let itemContent = name;
  if (isPacked) {
    itemContent = 
              <del>
                {name + "  "} 
              </del>
  }
  return (
    <li>{ itemContent }</li>
  )
}
export default function JudgeBian2() {
  return (
    <div>
      <h1>Sally Ride</h1>
      <ul>
        <Item name='' isPacked={true}/>
        <Item name='' isPacked={true}/>
        <Item name='Tam' isPacked={false}/>
      </ul>
    </div>
  )
}
