import React, { Fragment } from 'react'
import { people } from './37-people-data'
export default function ListFilter() {
  const newPerson = people.filter(ele => ele.profession === '');
  const item = newPerson.map(ele =>
    <Fragment key={ele.id}>
      <p>
        <b>{ele.name}:</b>
        {` ${ele.profession} ${ele.accomplishment}`}
      </p>
    </Fragment>
    )
  return (
      <ul>{item}</ul>
  )
}
