import { useState } from "react";

function Panel({title,onShow,isActive,children}) {
  return (
    <div style={{
      width: 400,
      border: '1px solid black',
      padding:10
    }}>
      <h3>{title}</h3>
      {isActive
        ?
        <p>{children}</p>
        :
        <button onClick={onShow}></button>
      }
    </div>
  )
}
export default function StateImprove(){
  const [isActive, setIsActive] = useState(0);
  return (
    <>
       <h2></h2>
      <Panel
        title=''
        isActive={isActive === 0}
        onShow={() => {setIsActive(0)}}  >
         200 1929  1997 
      </Panel>
      <Panel
        title=''
        isActive={isActive === 1}
        onShow={() => {setIsActive(1)}}  >
          <span lang="kk-KZ"></span><i lang="la">Malus sieversii</i> 
      </Panel>
    </>
  )
}